import { useState } from "react";
import {
  FluentProvider,
  teamsLightTheme,
  teamsDarkTheme,
  teamsHighContrastTheme,
  tokens,
} from "@fluentui/react-components";
import { useEffect } from "react";
import { app } from "@microsoft/teams-js";
import { useTeamsUserCredential } from "@microsoft/teamsfx-react";
import { TeamsFxContext } from "./utilities/Context";
import { config } from "./utilities/config.js";
import TabConfig from "./components/TabConfig";
import Privacy from "./components/Privacy";
import TermsOfUse from "./components/TermsOfUse";
import Tab from "./components/Tab";
import TokenInputs from "./components/TokenInputs";

function configurationPage() {
  if (window.location.pathname === "/configuration") {
    return <TabConfig />;
  }
}

function privacyPage() {
  if (window.location.pathname === "/privacy") {
    return <Privacy />;
  }
}

function termsOfUsePage() {
  if (window.location.pathname === "/termsofuse") {
    return <TermsOfUse />;
  }
}

function tab() {
  if (window.location.pathname === "/") {
    return (
      <>
        <Tab />
        <TokenInputs />
      </>
    );
  }
}

export default function App() {
  const { loading, themeString, teamsUserCredential } = useTeamsUserCredential({
    initiateLoginEndpoint: config.initiateLoginEndpoint,
    clientId: config.clientId,
  });

  let context;
  useEffect(() => {
    loading &&
      app.initialize().then(async () => {
        // Hide the loading indicator.
        app.notifySuccess();
        context = await app.getContext();
      });
  }, [loading]);

  return (
    <TeamsFxContext.Provider value={{ teamsUserCredential }}>
      <FluentProvider
        theme={
          themeString === "dark"
            ? teamsDarkTheme
            : themeString === "contrast"
            ? teamsHighContrastTheme
            : teamsLightTheme
        }
        style={{ background: tokens.colorNeutralBackground3 }}
      >
        {!loading && (
          <>
            {tab()}
            {configurationPage()}
            {privacyPage()}
            {termsOfUsePage()}
          </>
        )}
      </FluentProvider>
    </TeamsFxContext.Provider>
  );
}
