import { atlClientId, atlClientSecret, config } from "./config";

export const atlassianValidationQuery = `query validation {tenantContexts(hostNames:["sprinklr.atlassian.net"]) {cloudId}}`;

export const gitLabValidationQuery = "query {currentUser {name}}";

export const gitLabValidationUrl =
  "https://prod-gitlab.sprinklr.com/api/graphql";

export const atlassianValidationUrl = "https://api.atlassian.com/graphql";

export const atlassianPopupUrl =
  "https://auth.atlassian.com/authorize?audience=api.atlassian.com&client_id=HVy47ZY8PgWcRQRLs5VC8RwdqvuFRRbW&scope=offline%5faccess%20read%3Ame%20read%3Aaccount&redirect_uri=https%3A%2F%2Flocalhost%3A53000%2FatlassianAuth.html&state=abcd&response_type=code&prompt=consent";

export const atlassianRefreshUrl = "https://auth.atlassian.com/oauth/token";

export const popupAtlassian = function (setAtlassianInCache) {
  const popup = window.open(
    atlassianPopupUrl,
    "Atlassian Authentication",
    "width=700, height=1000,status=no, location=no, toolbar=no, menubar=no"
  );
  popup.addEventListener("beforeunload", () => {
    validateAtlassianToken(setAtlassianInCache);
  });
};

export const refreshAtlassianToken = async function () {
  const refreshToken = localStorage.getItem(`${config.clientId}:refresh_token`);
  if (refreshToken !== undefined && refreshToken !== null) {
    await fetch(atlassianRefreshUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        grant_type: "refresh_token",
        client_id: atlClientId,
        client_secret: atlClientSecret,
        refresh_token: refreshToken,
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        localStorage.setItem(
          `${config.clientId}:access_token`,
          response.access_token
        );
        localStorage.setItem(
          `${config.clientId}:refresh_token`,
          response.refresh_token
        );
      })
      .catch((error) => {
        console.log("Atlassian token refresh failed: ", error);
        localStorage.removeItem(`${atlClientId}:access_token`);
        localStorage.removeItem(`${atlClientId}:refresh_token`);
      });
  } else {
    localStorage.removeItem(`${atlClientId}:access_token`);
    localStorage.removeItem(`${atlClientId}:refresh_token`);
  }
};

export const validateAtlassianToken = async function (setAtlassianInCache) {
  refreshAtlassianToken();
  const accessToken = localStorage.getItem(`${config.clientId}:access_token`);
  if (accessToken !== null && accessToken !== undefined) {
    await fetch(atlassianValidationUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${accessToken}`,
      },
      body: JSON.stringify({
        query: atlassianValidationQuery,
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        console.log("Atlassian account authenticated: ", response);
        localStorage.removeItem(`${config.clientId}:access_token`);
        setAtlassianInCache(true);
      })
      .catch((error) => {
        console.log("Atlassian authentication faliled: ", error);
        localStorage.removeItem(`${config.clientId}:access_token`);
        localStorage.removeItem(`${config.clientId}:refresh_token`);
        setAtlassianInCache(false);
      });
  } else {
    localStorage.removeItem(`${atlClientId}:access_token`);
    localStorage.removeItem(`${atlClientId}:refresh_token`);
    setAtlassianInCache(false);
  }
};

export const validateGitLabToken = async function (
  gitLabToken,
  setGitInCache,
  setGitErrMsg
) {
  await fetch(gitLabValidationUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${gitLabToken}`,
    },
    body: JSON.stringify({
      query: gitLabValidationQuery,
    }),
  })
    .then((response) => {
      response.json();
    })
    .then((response) => {
      console.log(response);
      console.log("GitLab PAT validated: ", response);
      localStorage.setItem(`${config.clientId}:GitLab`, gitLabToken);
      setGitInCache(true);
      setGitErrMsg("");
    })
    .catch((error) => {
      console.log("GitLab PAT validation failed: ", error);
      setGitErrMsg(
        "The token you entered is not valid! Please enter a valid token."
      );
    });
};
