import { useState, useEffect } from "react";
import { TextField } from "@fluentui/react/lib/TextField";
import { Button } from "@fluentui/react-components";
import { Label } from "@fluentui/react/lib/Label";
import {
  validateAtlassianToken,
  gitLabValidationQuery,
  gitLabValidationUrl,
  validateGitLabToken,
  popupAtlassian,
} from "../utilities/tokenValidation";
import { config } from "../utilities/config";

export default function TokenInputs() {
  const [gitLabToken, setGitLabToken] = useState(
    localStorage.getItem(`${config.clientId}:GitLab`) === undefined
      ? ""
      : localStorage.getItem(`${config.clientId}:GitLab`)
  );

  const [gitInCache, setGitInCache] = useState(
    localStorage.getItem(`${config.clientId}:GitLab`) === null ? false : true
  );

  const [atlassianInCache, setAtlassianInCache] = useState(
    localStorage.getItem(`${config.clientId}:refresh_token`) === null
      ? false
      : true
  );

  useEffect(() => {
    if (gitInCache) {
      fetch(gitLabValidationUrl, {
        method: "POST",
        headers: {
          "Context-Type": "application/json",
          Authorization: `Bearer ${gitLabToken}`,
        },
        body: JSON.stringify(gitLabValidationQuery),
      }).catch((error) => {
        console.log("Removing the existing GitLab PAT as it is stale", error);
        localStorage.removeItem(`${config.clientId}:GitLab`);
        setGitInCache(false);
        setGitLabToken("");
      });
    }

    if (atlassianInCache) {
      validateAtlassianToken(setAtlassianInCache);
    }
  }, []);

  const [gitErrMsg, setGitErrMsg] = useState("");

  return (
    <>
      {(!gitInCache || !atlassianInCache) && (
        <div>
          <div>
            {!gitInCache && (
              <div>
                <TextField
                  label="Looks like we don't have your GitLab Personal access token. Enter your Personal access token below"
                  errorMessage={gitErrMsg}
                  onChange={(event) => setGitLabToken(event.target.value)}
                />
                <Button
                  appearance="primary"
                  onClick={() => {
                    validateGitLabToken(
                      gitLabToken,
                      setGitInCache,
                      setGitErrMsg
                    );
                  }}
                >
                  Validate
                </Button>
              </div>
            )}
          </div>
          <div>
            {!atlassianInCache && (
              <div>
                <Label>
                  Press the Button below to Authenticate the app as Atlassian
                  user. We need this to be able call API's
                </Label>
                <Button
                  appearance="primary"
                  onClick={() => {
                    popupAtlassian(setAtlassianInCache);
                  }}
                >
                  Authenticate
                </Button>
              </div>
            )}
          </div>
        </div>
      )}
    </>
  );
}
